body {
  font-family: "Noto Sans", sans-serif;
  font-family: "Sofia Sans", sans-serif;
  --text-color: #f4e1b1;
  --heading-color: #ffffff;
  --darkest: #242526;
  --mid-tone: #292929;
  --light-tone: #505253;
  --accent: #df9f3c;
}

//shaded background for info bar
.shade-header {
  background: linear-gradient(var(--darkest), #18191ae1, #18191ac5, #18191a00);
  animation: shading 2s linear;
}

@keyframes shading {
  0% {
    opacity: (0);
  }

  100% {
    transform: (1);
  }
}

//Info Bar Design
.info-bar {
  position: sticky;
  top: 0;
  margin-top: 40vh;
  padding-bottom: 7rem;
  display: flex;
  flex-direction: column;
  align-items: center;
  z-index: 99;
  height: 10rem;

  //intro animation
  .left,
  .right,
  .socials {
    animation: fadeIn 7s;
  }

  @keyframes fadeIn {
    0% {
      opacity: 0;
    }

    20% {
      opacity: 0;
    }

    100% {
      opacity: 1;
    }
  }

  .contact {
    margin: -0.5rem;
    display: grid;
    grid-template-columns: 1fr 1fr 1fr;
    justify-items: center;
    align-items: center;
    font-size: 1.5rem;
    gap: 1rem;
    z-index: 100;

    h1 {
      font-size: 4rem;
      text-align: center;
      color: var(--heading-color);
    }

    a:hover {
      color: var(--accent);
    }
  }

  a {
    color: var(--heading-color);
    cursor: pointer;
    padding: 1rem;
    border-radius: 50%;
    transition: 0.2s ease-in-out;

    &:hover {
      color: var(--accent);
      text-shadow: -1px -1px 2px #000, 1px -1px 2px #000, -1px 1px 2px #000,
        1px 1px 2px #000;
      transform: scale(1.5);
    }

    &:visited {
      color: var(--heading-color);
    }
  }

  .socials {
    margin-top: -2rem;
    display: flex;
    flex-wrap: wrap;
    scale: 2;
    padding-inline: 5px;
    gap: 0.8rem;
    z-index: 100;

    a:hover {
      color: var(--accent);
    }
  }
}

//change colour on splash screen
.black {
  .contact {
    h1,
    a {
      color: #000;
    }
  }

  .socials {
    a {
      color: #000;
    }
  }
}

//Body
body {
  background-color: var(--darkest);

  h3,
  h4,
  h5 {
    color: var(--heading-color);
  }
}

.section {
  // .nova {
  //   text-decoration: none;
  //   font-family: "Trebuchet MS", "Lucida Sans Unicode", "Lucida Grande",
  //     "Lucida Sans", Arial, sans-serif;
  // }

  p,
  li {
    font-family: "Noto Sans", sans-serif;
    font-weight: 300;
    list-style-type: circle;
  }

  li {
    text-align: left;
  }

  .main {
    font-size: 1.5rem;
    margin-inline: auto;
    overflow: hidden;
    margin-top: -50rem;
    background-color: #242526;
    color: var(--text-color);

    .splash-page {
      min-height: 67.5rem;
      background-image: url(/resume/assets/launchpadhero.webp);
    }

    .card {
      margin-inline: auto;
      padding-inline: clamp(4.625rem, 8%, 10%);

      p,
      li,
      h4 {
        position: relative;
        z-index: 1;
      }
    }

    .about-me {
      position: relative;
      min-height: 100vh;
      display: flex;
      flex-direction: column;
      justify-content: center;
      overflow: hidden;

      .about {
        font-size: 19rem;
        font-weight: 1000;
        letter-spacing: -1rem;
        width: fit-content;
        position: absolute;
        top: 0;
        left: 0;
        right: 0;
        margin-inline: auto;
        margin-block: 2rem;
        --outline-color: #000;
        background: linear-gradient(45deg, var(--darkest), #5e4026);
        background-size: 800%;
        background-clip: text;
        -webkit-background-clip: text;
        animation: head-animate 20s alternate infinite;
        color: transparent;
      }

      .flex-wrapper {
        margin-inline: auto;
        margin-top: 9.5rem;
        display: flex;
        justify-content: center;
        gap: 3vw;
        max-width: 83rem;
        .left-wrapper {
          max-width: 55rem;
          flex-shrink: 3;
        }
        .skills {
          display: flex;
          gap: 2rem;
          flex-direction: column;
        }
      }
      .right-grad,
      .left-grad {
        position: absolute;
        width: 7vw;
        height: 1000vw;
        animation: about-grad 10s alternate infinite;
      }

      @keyframes about-grad {
        from {
          opacity: 0%;
        }

        to {
          opacity: 100%;
        }
      }
      .left-grad {
        left: 0;
        border-right: #f4e1b1 solid 2px;
        background-image: linear-gradient(
          90deg,
          var(--darkest),
          var(--darkest),
          #df9f3c
        );
      }
      .right-grad {
        right: 0;
        border-left: #f4e1b1 solid 2px;
        background-image: linear-gradient(
          -90deg,
          var(--darkest),
          var(--darkest),
          #df9f3c
        );
      }
    }

    .experience {
      background-image: url("/resume/kiwihug-MS9Tnh3if1o-unsplash.jpg");
      background-size: cover;
      padding-block: 8rem;
      position: relative;
      overflow: hidden;
      .exp-wrapper {
        position: absolute;
        top: 3rem;
        left: 0;
        margin-block: 2rem;
        width: 12.5rem;
        overflow: hidden;
      }
      .exp {
        position: relative;
        letter-spacing: -2.75rem;
        left: -2rem;
        font-size: 6rem;
        font-weight: 1000;
        color: #5e4026;
        writing-mode: vertical-rl;
        text-orientation: upright;
      }

      p,
      li {
        font-weight: 300;
        color: #000;
      }

      h3,
      h4,
      h5 {
        color: #000;
      }

      .slide {
        max-width: 83rem;
        overflow: hidden;
        left: 0;
        right: 0;
        margin-inline: auto;
      }
      .carousel-inner {
        display: flex;
        overflow-x: scroll;
        scrollbar-color: #5e4026 #00000000;
        scroll-snap-type: x mandatory;
        scroll-behavior: smooth;
        -webkit-overflow-scrolling: touch;
      }
      .carousel-inner::-webkit-scrollbar {
        width: 10px;
        height: 10px;
      }
      .carousel-inner::-webkit-scrollbar-thumb {
        background: #5e4026;
        border-radius: 10px;
      }

      .carousel-inner::-webkit-scrollbar-track {
        background: transparent;
      }
      .carousel-item {
        scroll-snap-align: start;
        flex-shrink: 0;
        max-width: 72rem;
        margin-right: 50px;
        display: flex;
        flex-direction: column;
        font-size: 1.5rem;
      }
    }

    .community {
      padding-block: 8rem;
      position: relative;

      .aas {
        padding-left: 10rem;
      }

      .cynon {
        padding-right: 18rem;
      }

      .on1 {
        position: absolute;
        z-index: 0;
        height: 300px;
        background: linear-gradient(to right, var(--accent), var(--mid-tone));
        border: rgba(255, 255, 255, 0.2) ridge;
        border-width: 0px 0px 0px 5px;
        opacity: 0.5;
        top: 27%;
        right: 0;
        transform: skewY(-5deg);
        animation: comm-right 5s linear;
        animation-fill-mode: forwards;
      }

      .on2 {
        position: absolute;
        z-index: 0;
        height: 300px;
        background: linear-gradient(to left, var(--accent), var(--mid-tone));
        border: rgba(255, 255, 255, 0.2) groove;
        border-width: 0px 5px 0px 0px;
        opacity: 0.5;
        top: 69%;
        left: 0;
        transform: skewY(-5deg);
        animation: comm-left 5s linear;
        animation-fill-mode: forwards;
      }

      @keyframes comm-right {
        from {
          width: 0%;
        }

        to {
          width: 84%;
        }
      }

      @keyframes comm-left {
        from {
          width: 0%;
        }

        to {
          width: 72%;
        }
      }
    }

    .education {
      padding-block: 8rem;

      .edu-hr {
        color: transparent;
      }

      .activate {
        position: relative;
        background: linear-gradient(to right, #a52a2a, #a52a2a00);
        border: 0;
        height: 0.125rem;
        margin-left: -11%;
        animation: edu-stripe 2s linear;
      }

      @keyframes edu-stripe {
        from {
          width: 0%;
        }

        to {
          width: 100%;
        }
      }
    }

    // .heading {
    //   font-family: "Sofia Sans", sans-serif;
    //   font-size: 2.5rem;
    //   font-weight: 700;
    //   z-index: 0;
    //   width: fit-content;
    //   background: linear-gradient(45deg, var(--text-color), var(--accent));
    //   background-size: 800%;
    //   background-clip: text;
    //   -webkit-background-clip: text;

    //   color: transparent;
    // }

    @keyframes head-animate {
      from {
        background-position: left;
      }

      to {
        background-position: right;
      }
    }

    .space {
      margin-top: 0px;
      margin-bottom: 0px;
      display: flex;
      align-items: center;
      justify-content: space-between;

      > * {
        margin-bottom: 0px;
      }
    }

    .main-title {
      margin-bottom: 0px;
      font-size: 2rem;
    }

    .sub-title {
      position: relative;
      left: 1rem;
      margin-top: 0px;
      font-weight: 700;
    }

    .certificates {
      display: flex;
      flex-wrap: wrap;
      flex-direction: row;
      justify-content: center;
      gap: 20%;

      .qual {
        display: flex;
        flex-direction: column;
        flex-wrap: wrap;
        align-items: center;

        .level {
          font-size: 1.5rem;
          margin: 0;
        }
      }
    }
  }
}

@media (max-width: 1200px) {
  .section {
    .nova {
      display: none;
    }
  }
  .info-bar {
    height: 1rem;
    transition: height 0.5s ease-in-out;

    .models-3d {
      display: none;
    }
    .contact {
      display: flex;
      flex-wrap: wrap;
      justify-content: center;
      gap: 0rem;

      h1,
      p {
        margin-block: 0.5rem;
      }

      p {
        visibility: hidden;
        opacity: 0;
      }
    }

    .contact :nth-child(1) {
      order: 2;
    }

    .contact :nth-child(2) {
      width: 100%;
    }

    .socials {
      visibility: hidden;
      opacity: 0;
    }
  }

  .info-bar:hover,
  .info-bar:focus {
    height: 10rem;
    transition: height 0.5s ease-in-out;

    .contact {
      p {
        padding-inline: 4rem;
        visibility: visible;
        opacity: 1;
        transition: opacity 1s ease-in-out;
      }
    }

    .socials {
      visibility: visible;
      opacity: 1;
      transition: opacity 1s ease-in-out;
      gap: 0px;
      scale: 1.5;
      margin-top: 1rem;
    }
  }

  .section {
    .main {
      .community {
        padding-bottom: 0rem;

        .aas {
          padding-left: 0rem;
        }

        .cynon {
          padding-right: 0rem;
        }

        .on1 {
          top: 33%;
        }

        .on2 {
          top: 79%;
        }

        @keyframes comm-right {
          from {
            width: 0%;
          }

          to {
            width: 94%;
          }
        }

        @keyframes comm-left {
          from {
            width: 0%;
          }

          to {
            width: 94%;
          }
        }
      }
    }
  }
}

@media (max-width: 600px) {
  .info-bar {
    overflow: hidden;

    .contact {
      h1 {
        font-size: 200%;
      }
    }
  }

  .section {
    .main {
      .about-me {
        .skill-grid {
          color: var(--text-color);
          display: grid;
          grid-template-rows: repeat(8, 1fr);
          grid-template-columns: 1fr;
          row-gap: 1rem;
          column-gap: 1rem;
        }
      }

      .space {
        flex-direction: column;
        justify-content: center;

        .main-title {
          margin-top: 0px;
          text-align: center;
        }
      }

      .experience {
        padding-bottom: 0rem;

        .sub-title {
          text-align: center;
          left: 0;
        }
      }

      .community {
        .on1 {
          top: 32rem;
        }

        .on2 {
          top: 78rem;
        }
      }
    }
  }
}
